.footer {
  background: url(../../img/footer.png)0 0 no-repeat;
  background-size: cover;
  padding: 70px 0 140px;

  &__row {
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;

    &:last-child {
      margin-bottom: 0;
    }

    &--col {
      flex-direction: column;
      align-items: center;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 10px;
    text-transform: uppercase;
  }

  &__categories {
    .menu {
      &__list {
        flex-direction: column;
        align-items: flex-start;
      }

      &__item {
        margin-right: 0;
      }

      &__link {
        color: #fff;
        font-size: 24px;
      }
    }
  }

  &__contacts {
    .contacts {
      &__address,
      &__mail {
        font-size: 24px;
        font-weight: 400;
        color: #fff;
      }

      &__address {
        margin-bottom: 10px;
      }
    }
  }

  &__text {
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    margin-bottom: 10px;
  }

  &__buttons{
      margin-top: 15px;
  }

  &__input {
    width: 400px;
    max-width: 100%;

    .input {
      width: 100%;

    }
  }
}
