.about {
  margin-top: 48px;
}

.about__item {
  margin-bottom: 50px;
  display: flex;
}

.about__item:last-child {
  margin-bottom: 0;
}

.about__item--reverse .about__img {
  margin-right: 0;
  margin-left: 20px;
}

.about__img {
  margin-right: 20px;
  height: 480px;
  width: 600px;
  flex-shrink: 0;
}

.about__pic {
  max-width: 100%;
  max-height: 100%;
}

.about__subtitle {
  font-size: 50px;
  font-weight: 700;
  color: #0d689e;
  margin-bottom: 10px;
  text-align: center;
}

.about__text {
  font-size: 22px;
  color: #053752;
}

.footer {
  background: url(../../img/footer.png) 0 0 no-repeat;
  background-size: cover;
  padding: 70px 0 140px;
}

.footer__row {
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
}

.footer__row:last-child {
  margin-bottom: 0;
}

.footer__row--col {
  flex-direction: column;
  align-items: center;
}

.footer__title {
  font-size: 28px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.footer__categories .menu__list {
  flex-direction: column;
  align-items: flex-start;
}

.footer__categories .menu__item {
  margin-right: 0;
}

.footer__categories .menu__link {
  color: #fff;
  font-size: 24px;
}

.footer__contacts .contacts__address,
.footer__contacts .contacts__mail {
  font-size: 24px;
  font-weight: 400;
  color: #fff;
}

.footer__contacts .contacts__address {
  margin-bottom: 10px;
}

.footer__text {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  text-align: center;
  margin-bottom: 10px;
}

.footer__buttons {
  margin-top: 15px;
}

.footer__input {
  width: 400px;
  max-width: 100%;
}

.footer__input .input {
  width: 100%;
}

.header {
  padding: 10px 0 30px;
}

.header__row {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__row:last-child {
  margin-bottom: 0;
}

.logo__link {
  display: inline-block;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 10px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item--active .menu__link {
  font-weight: 700;
}

.menu__link {
  font-size: 18px;
}

.preview {
  background: url(../../img/preview.png) center no-repeat;
  background-size: cover;
  min-height: 820px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview__title {
  font-size: 97px;
  font-weight: 700;
  color: #fff;
}

.socials__list {
  display: flex;
  align-items: center;
}

.socials__item {
  margin-right: 10px;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: inline-block;
  background-color: blue;
  height: 30px;
  width: 30px;
  border-radius: 5px;
  position: relative;
}

.socials__link::after {
  content: '';
  display: inline-block;
  position: absolute;
  height: 20px;
  width: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../../img/fc.png) center no-repeat;
  background-size: cover;
  z-index: 1;
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

button,
input {
  border: 1px solid transparent;
  outline: none;
}

button {
  cursor: pointer;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

.input {
  border-color: #0d689e;
  border-radius: 6px;
  font-size: 16px;
  color: #0d689e;
  padding: 6px 5px;
}

.button--fot {
  background-color: #fff;
  padding: 5px 8px;
  font-size: 17px;
  color: #0d689e;
}