.about {
  margin-top: 48px;
  &__item {
    margin-bottom: 50px;
    display: flex;

    &:last-child {
      margin-bottom: 0;
    }

    &--reverse {
      .about__img {
        margin-right: 0;
        margin-left: 20px;
      }
    }
  }

  &__img {
    margin-right: 20px;
    height: 480px;
    width: 600px;
    flex-shrink: 0;
  }

  &__pic {
    max-width: 100%;
    max-height: 100%;
  }

  &__subtitle {
    font-size: 50px;
    font-weight: 700;
    color: #0d689e;
    margin-bottom: 10px;
    text-align: center;
  }

  &__text {
    font-size: 22px;
    color: #053752;
  }
}
