*{
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

button,
input {
  border: 1px solid transparent;
  outline: none;
}

button {
  cursor: pointer;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.container {
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

.input {
  border-color: #0d689e;
  border-radius: 6px;
  font-size: 16px;
  color: #0d689e;
  padding:6px 5px;
}

.button {
  &--fot {
    background-color: #fff;
    padding: 5px 8px;
    font-size: 17px;
    color: #0d689e;
  }
}
